export default {
	props:["pid"],
	data() {
		return {
			detail:[]
		}
	},
	methods: {
		getDetail(){
			uni.request({
				url:"https://api.bailiban.com/api/admin/miniprogram/project/plan/detail/list?projectId="+this.pid,
				header:{
					"Authorization":uni.getStorageSync("blbUI-token")
				},
				method:"GET",
				dataType:"json",
				success:(res)=> {
					console.log(res)
					if(res.data.status == 200){
						this.detail = res.data.data
					}else{
						uni.showToast({
							title:'请求数据失败'
						})
					}
				},
				fail() {
					uni.showToast({
						title:'请求接口失败'
					})
				}
			});
		},
		goDetailInfo(id){
			uni.navigateTo({
				url:"../../pages/project/detailInfo?id="+id
			})
		}
	},
	//组件调用后
	created(){
		this.getDetail()
	}
}
